:host {

  p {
    text-align: center;
    display: inline;
  }

  .font {

    &-section {
      font-size: 32px; // TITLE_SECTION
      line-height: 36px;
    }

    &-name {
      font-size: 24px; // TITLE_NAME
      line-height: 28px;
      letter-spacing: -0.02em;
    }

    &-name-regular {
      font-size: 24px;
      line-height: 28px;
      letter-spacing: -0.02em;
    }

    &-card {
      font-size: 18px; // TITLE_CARDS
      line-height: 20px;
      letter-spacing: -0.008em;
    }

    &-card-title {
      font-size: 10px; // LEGAL
      line-height: 16px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
    }

    &-card-subtitle {
      font-size: 14px; // LEGAL
      line-height: 22px;
      letter-spacing: 0.02em;
    }

    &-input {
      font-size: 14px; // INPUTS
      line-height: 22px;
      letter-spacing: 0.02em;
    }

    &-input-regular {
      font-size: 14px; // INPUTS
      line-height: 18px;
      letter-spacing: 0.02em;
    }

    &-input-medium {
      font-size: 14px; // INPUTS
      line-height: 18px;
      letter-spacing: 0.02em;
    }

    &-normal-omnes {
      font-size: 14px; // INPUTS
      line-height: 22px;
      letter-spacing: 0.02em;
    }

    &-notification {
      font-size: 13px; // NOTIFICATION
      line-height: 15px;
      letter-spacing: 0.02em;
    }

    &-alert {
      font-size: 13px;
      line-height: 15px;
      letter-spacing: 0.02em;
    }

    &-label {
      font-size: 10px; // LABEL

      line-height: 16px;
      letter-spacing: 0.02em;
    }

    &-menu {
      font-size: 11px; // MENÃš
      line-height: 16px;
      letter-spacing: 0.05em;
    }

    &-info {
      font-size: 14px; // INFO
      line-height: 16px;
      letter-spacing: 0.1em;
    }

    &-tab {
      font-size: 14px;
      line-height: 16px;
    }

    &-child-card-title {
      font-size: 14px;
      line-height: 18px;
      font-weight: 600;
      margin: 0 !important;
      letter-spacing: 0.02em;
    }

    &-child-card-title-regular {
      font-size: 14px;
      line-height: 18px;
      font-weight: 600;
      margin: 0 !important;
      letter-spacing: 0.02em;
    }

    &-rate-list-title {
      font-size: 18px;
      line-height: 20px;
      letter-spacing: -0.008em;
    }

    &-endorsement-rate-list-title {
      font-weight: 600;
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.02em;
    }

    &-child-card-link {
      font-size: 11px;
      line-height: 16px;
      margin: 0 !important;
      letter-spacing: 0.05em;
    }

    &-legal {
      font-size: 11px;
      line-height: 16px;
      letter-spacing: 0.02em;
    }

    &-label-bold {
      font-size: 11px;
      line-height: 16px;
      letter-spacing: 0.02em;
    }

    &-button_md {
      font-size: 14px;
      font-weight: 700;
      line-height: 16.34px;
      letter-spacing: 0.02em;
    }

    &-button_sm {
      font-size: 12px;
      font-weight: 700;
      line-height: 14.98px;
      letter-spacing: 0.02em;
    }

    &-paragraph {
      font-size: 14px;
      line-height: 22px;
    }

    &-paragraph_todter {
      font-size: 14px;
      line-height: 16px;
    }

    &-subheader-cell-table {
      font-size: 13px;
      line-height: 15px;
      letter-spacing: 0.02em;
      font-weight: 600;
    }

    &-cell-table {
      font-size: 13px;
      line-height: 15px;
      letter-spacing: 0.02em;
    }

    &-cell-table-md {
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 0.02em;
    }
  }

  .color {

    &-primary {
      color: #8f9bb3 !important;
    }

  }

  .weight {

    &-bold {
      font-weight: bold;
    }
  }

  .ellipsis {
    display: inline-block;
    white-space: nowrap;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 0;
    text-align: left;
    margin-top: 8px;
  }

  .blocked {
    display: block !important;
    text-align: left !important;
    margin: 0px !important;
  }
}
